@import url('https://fonts.googleapis.com/css2?family=Delius&display=swap');

*{
  -webkit-tap-highlight-color: transparent;
  user-select: none; 
}
.Loader {
  text-align: center;
  height: 50vh;
  display: grid;
  grid-template-rows: 2fr 6fr;
  justify-content: center;
  /* align-items: center; */
}

h3, h1{
  font-family: "Delius";
}


.loader-box{
  height: 25vh;
  width: 40vh;
  background-color: rgb(237, 237, 237);
  display: grid;
  place-items: center;
}

.loader{
  height: 10px;
  width: 80%;
  background-color: white;
  border-radius: 5px;
  overflow: hidden;
}

.loader-fill{
    height: 100%;
  background-color: hsl(227, 90%, 40%);
  transition: 500ms;
}

.load-button{
  background-color: rgb(58, 197, 39);
  color: white;
  font-weight: 500;
  font-size: 30px;
  width: 40px;
  height: fit-content;
  border-radius: 10px;
  cursor: pointer;
}

.control{
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  gap: 20px;
  align-items: center;
}
