.drop_face{
    display: grid;
    grid-template-columns: 5fr 1fr;
    align-items: center;
}

.dropdown{
    overflow: hidden;
    /* border: 1px solid black; */
}
svg{
    fill: rgb(122, 122, 122);
}

#svg{
    position: absolute;
    right: 10px;
    transform: rotateZ(0);
    transition: 100ms;
}

#info{
    /* transition: 2000ms;
    transform: translateY(100px); */
    height: fit-content;
    padding: 10px;
    font-family: 'Courier New', Courier, monospace;
}

