img{
    height: 28vh;
    object-fit: contain;
}

.contain{
    width: 60vw;
    text-align: center;
    opacity: 100%;
    transition: 200ms;
}

.image_container{
    width: 100vw;
    display: flex;
    gap: 10vw;
}

.svg_arrow{
    height: 30vh;
    width: 10vw;
    background-color: rgb(147, 147, 147);
}