#svg-c{
    height: 50px;
    width: 50px;
    background-color: rgb(199, 199, 199);
    text-align: center;
    align-content: center;
    border-radius: 30%;
    border: 2px solid black;
    position: absolute;
    top: 5px;
    right: 5px;
}